import React, { Component, Suspense } from 'react';
import { Modal, Dropdown } from 'react-bootstrap';
import PerfectScrollbar from 'react-perfect-scrollbar';
import { OverlayTrigger, Tooltip, Button } from 'react-bootstrap';
import SVG from 'react-inlinesvg';
import { baseUrl } from '../../../../Assets/AssetsHelper';
import { ContextMenu, MenuItem, ContextMenuTrigger, SubMenu } from "react-contextmenu";
import { Link } from 'react-router-dom';
import { connect } from "react-redux";
import { GET_MEDIA_NAME } from '../../../../Services/Utils/utils'
import Swal from 'sweetalert2';

const ModalMedia = React.lazy(() => import('./Elements/ModalMedia'));
const AssetsModalAside = React.lazy(() => import('./Elements/AssetsModalAside'));
const MoveTo = React.lazy(() => import('../Components/Actions/MoveTo'));

class AssetsModal extends Component {

    state = {
    };

    handleClose = () => {
        this.props.modalCallback(false)
    };
    handleDragDrop = () => {

    };
    downloadWarning = (modalAssetData) => {
        Swal.fire({
            title: 'Are you sure ?',
            text: "This image is not copyrighted, hence we suggest you take the UGC rights before downloading.",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonText: 'Yes, Download!'
          }).then((result) => {
            if (result.isConfirmed) {
                this.props.exportDonwloadMedia(modalAssetData.postFile, modalAssetData.type, modalAssetData.mediaUrl)
            }
          })
    }
    render() {
        const { modalToggle, modalAssetData, exportDonwloadMedia, rightsHandleModalShow, addTagModalShow, postPostCopy, deleteAsset, collections, networkData, isTrash, onClickPutBack, onClickDeletePermanently, editMediaModalShow, postPostMove, postPostUpdate, currentIndex } = this.props

        return <Modal size={'full'} show={modalToggle} className="assets_modal border-0 p-0" backdrop={false}>
            <Modal.Header className="border-0 p-0">
                <div className="ass_modal_actions d-flex flex-grow-1 justify-content-lg-end justify-content-between">
                    <div className="d-flex align-items-center flex-wrap">
                        <div className="d-none d-lg-flex">
                            {
                                isTrash ? <>
                                    <Button variant={'link'} className="text-decoration-none p-0 d-inline-flex align-items-center me-3" onClick={onClickPutBack(modalAssetData)}>
                                        <i className="ri-restart-line icon-6 me-1"></i>
                                        <span className="text-gray-800 fs-7">Restore</span>
                                    </Button>

                                    <Button variant={'link'} className="text-decoration-none p-0 d-inline-flex align-items-center me-3" onClick={onClickDeletePermanently(modalAssetData)}>
                                        <i className="ri-delete-bin-6-line icon-6 me-1"></i>
                                        <span className="text-gray-800 fs-7">Delete Permanently</span>
                                    </Button>
                                </> : <>

                                    <Button variant={'link'} className="text-decoration-none p-0 d-inline-flex align-items-center me-3" onClick={editMediaModalShow ? editMediaModalShow(modalAssetData) : null}>
                                        <span className="svg-icon text-dark">
                                            <SVG src={baseUrl('media/icons/svg-icons/magic-stick.svg')} height={15} width={15} />
                                        </span>
                                        <span className="menu-title">Edit</span>
                                    </Button>
                                    <Button variant={'link'} className="text-decoration-none p-0 d-inline-flex align-items-center me-3" onClick={addTagModalShow ? addTagModalShow(modalAssetData) : null}>
                                        <i className="ri-price-tag-3-line icon-6 me-1 text-dark"></i>
                                        <span className="text-gray-800 fs-7">Add Tag</span>
                                    </Button>
                                    <Dropdown align="start" className="block-center position-relative">
                                        <Dropdown.Toggle variant="link" className="text-decoration-none p-0 d-inline-flex align-items-center me-2 arrow_disabled" >
                                            <i className="ri-file-copy-line icon-6 me-1 text-dark"></i>
                                            <span className="menu-title">Copy</span>
                                        </Dropdown.Toggle>
                                        <Dropdown.Menu style={{ width: 230 }}>
                                            <PerfectScrollbar options={{ wheelPropagation: false }}>
                                                <div style={{ minHeight: 180, maxHeight: 380 }}>
                                                    {collections && collections.length > 0 ?
                                                        collections.map((cItem, index) =>
                                                            <div key={index} className="menu-item text-decoration-none">
                                                                <MenuItem className="dropdown-item" onClick={postPostCopy ? postPostCopy({ collection: String(modalAssetData.collection), destination: String(cItem.id), id: modalAssetData.id }) : null}>
                                                                    <div className="symbol symbol-24 flex-shrink-0 me-2 bg-light" style={{ borderRadius: 2 }}>
                                                                        {cItem.thumbnails ? <img className="img-fluid object-center-cover" src={cItem.thumbnails} height={54} width={54} alt={GET_MEDIA_NAME(cItem.thumbnails)} /> : <i className="ri-folder-open-line"></i>}
                                                                    </div>
                                                                    <span className="menu-title">{cItem.name}</span>
                                                                </MenuItem>
                                                            </div>
                                                        ) : null}
                                                </div>
                                            </PerfectScrollbar>
                                        </Dropdown.Menu>
                                    </Dropdown>

                                    <Dropdown align="start" className="block-center position-relative">
                                        <Dropdown.Toggle variant="link" className="text-decoration-none p-0 d-inline-flex align-items-center me-2 arrow_disabled">
                                            <i className="ri-arrow-right-circle-line icon-6 me-1 text-dark"></i>
                                            <span className="text-gray-800 fs-7">Move to</span>
                                        </Dropdown.Toggle>

                                        {collections && collections.length > 0 ?
                                            <Dropdown.Menu style={{ width: 230 }}>
                                                <PerfectScrollbar>
                                                    <div style={{ minHeight: 180, maxHeight: 380 }}>
                                                        {
                                                            collections.map((cItem, index) => <div key={index} className="menu-item" onClick={postPostMove ? postPostMove(modalAssetData.id, modalAssetData.collection, cItem.id) : null}>
                                                                <Dropdown.Item>
                                                                    <div className="symbol symbol-24 flex-shrink-0 me-2 bg-light" style={{ borderRadius: 2 }}>
                                                                        {cItem.thumbnails ? <img className="img-fluid object-center-cover" src={cItem.thumbnails} height={54} width={54} alt={GET_MEDIA_NAME(cItem.thumbnails)} /> :
                                                                            <i className="ri-folder-open-line"></i>
                                                                        }
                                                                    </div>
                                                                    <span className="menu-title text-break">{cItem.name}</span>
                                                                </Dropdown.Item>
                                                            </div>
                                                            )
                                                        }
                                                    </div>
                                                </PerfectScrollbar>
                                            </Dropdown.Menu>
                                            : null}
                                    </Dropdown>

                                    {(modalAssetData && modalAssetData.Feed && Object.keys(modalAssetData.Feed).length > 0 && (modalAssetData.networkId == 1 || (modalAssetData.networkId == 3 && modalAssetData.Feed.filterId == 62) || modalAssetData.networkId == 7 || modalAssetData.networkId == 8 || modalAssetData.networkId == 6 || modalAssetData.networkId == 11 || modalAssetData.networkId == 21 || (modalAssetData.networkId == 18 && (modalAssetData.Feed.filterId == 24 || modalAssetData.Feed.filterId == 25 || modalAssetData.Feed.filterId == 26)) || modalAssetData.networkId == 28  || modalAssetData.Feed.name == "Chrome Extension")) ? <button type="button" className="btn btn-link p-0 d-inline-flex align-items-center me-3 text-decoration-none" onClick={rightsHandleModalShow ? rightsHandleModalShow(modalAssetData) : null}>
                                        <span className="svg-icon text-dark">
                                            <SVG src={baseUrl('media/icons/svg-icons/rights-line.svg')} height={15} width={15} />
                                        </span>
                                        <span className="text-gray-800 fs-7">Get Rights</span>
                                    </button> : null}
                                    <Dropdown align="start" className="block-center position-relative">
                                        <Dropdown.Toggle variant="link" className="text-decoration-none p-0 d-inline-flex align-items-center me-2 arrow_disabled" >
                                            <span className="svg-icon text-dark">
                                                <SVG src={baseUrl('media/icons/svg-icons/publish-line.svg')} height={15} width={15} />
                                            </span>
                                            <span className="text-gray-800 fs-7">Publish to</span>
                                        </Dropdown.Toggle>
                                        <Dropdown.Menu style={{ width: 230 }}>
                                            <PerfectScrollbar>
                                                <div className="submenu_contxt_wrap">
                                                    <div className="menu-item text-decoration-none">
                                                        <MenuItem className="dropdown-item" onClick={event => this.props.history(`/social-publish?assetId=${modalAssetData.id}`)}>
                                                            <i className="ri-navigation-line icon-6 me-1" style={{ transform: 'scaleX(-1)' }}></i>
                                                            <span className="menu-title">Social Publish</span>
                                                        </MenuItem>
                                                    </div>
                                                    <div className="menu-item text-decoration-none">
                                                        <MenuItem className="dropdown-item" onClick={event => this.props.history(`/print-media?assetId=${modalAssetData.id}`)}>
                                                            <span className="svg-icon svg-icon-5">
                                                                <SVG src={baseUrl('media/icons/svg-icons/print-media-2.svg')} height={15} width={15} />
                                                            </span>
                                                            <span className="menu-title">Print Media</span>
                                                        </MenuItem>
                                                    </div>
                                                    <div className="menu-item text-decoration-none">
                                                        <MenuItem className="dropdown-item disabled" onClick={event => this.props.history('/social-ads')}>
                                                            <OverlayTrigger placement="top" overlay={<Tooltip>Coming soon</Tooltip>}><>
                                                                <span className="svg-icon svg-icon-5">
                                                                    <SVG src={baseUrl('media/icons/svg-icons/speaker-line.svg')} height={15} width={15} />
                                                                </span>
                                                                <span className="menu-title">Social Ads</span>
                                                            </>
                                                            </OverlayTrigger>
                                                        </MenuItem>
                                                    </div>
                                                </div>
                                            </PerfectScrollbar>
                                        </Dropdown.Menu>
                                    </Dropdown>
                                    <Button variant={'link'} className="text-decoration-none p-0 d-inline-flex align-items-center me-3" onClick={this.props.updateAssetStatus}>
                                        <i className={`icon-6 me-1 ${modalAssetData.status == 2 ? 'ri-eye-line' : 'ri-eye-off-line'}`}></i>
                                        <span className="text-gray-800 fs-7">{modalAssetData.status == 2 ? 'Show' : 'Hide'}</span>
                                    </Button>
                                </>
                            }

                        </div>
                        {!isTrash ? <Dropdown align="end" className="block-center position-relative">
                            <Dropdown.Toggle variant="link" className="text-decoration-none p-1 arrow_disabled d-flex align-items-center">
                                <i className="ri-more-2-fill icon-6 me-0"></i>
                                <span className="text-gray-800 fs-7 d-flex d-lg-none">Actions</span>
                            </Dropdown.Toggle>

                            <Dropdown.Menu>
                                <div className="d-block d-lg-none">
                                    <div className="menu-item">
                                        <Dropdown.Item onClick={editMediaModalShow ? editMediaModalShow(modalAssetData) : null}>
                                            <span className="svg-icon">
                                                <SVG src={baseUrl('media/icons/svg-icons/magic-stick.svg')} height={15} width={15} />
                                            </span>
                                            <span className="menu-title">Edit</span>
                                        </Dropdown.Item>
                                    </div>
                                    <div className="menu-item">
                                        <Dropdown.Item onClick={addTagModalShow ? addTagModalShow(modalAssetData) : null}>
                                            <i className="ri-price-tag-3-line icon-6 me-1 text-dark"></i>
                                            <span className="menu-title">Add Tag</span>
                                        </Dropdown.Item>
                                    </div>
                                    {(modalAssetData && modalAssetData.Feed && Object.keys(modalAssetData.Feed).length > 0 && (modalAssetData.networkId == 1 || (modalAssetData.networkId == 3 && modalAssetData.Feed.filterId == 62) || modalAssetData.networkId == 7 || modalAssetData.networkId == 8 || modalAssetData.networkId == 6 || modalAssetData.networkId == 11 || modalAssetData.networkId == 21 || (modalAssetData.networkId == 18 && (modalAssetData.Feed.filterId == 24 || modalAssetData.Feed.filterId == 25 || modalAssetData.Feed.filterId == 26)) || modalAssetData.networkId == 28)) ?
                                        <div className="menu-item" onClick={deleteAsset ? deleteAsset(modalAssetData) : null}>
                                            <Dropdown.Item onClick={rightsHandleModalShow ? rightsHandleModalShow(modalAssetData) : null}>
                                                <span className="svg-icon">
                                                    <SVG src={baseUrl('media/icons/svg-icons/rights-line.svg')} height={15} width={15} />
                                                </span>
                                                <span className="menu-title">Get Rights</span>
                                            </Dropdown.Item>
                                        </div> : null}
                                </div>
                                <div className="menu-item" onClick={exportDonwloadMedia ? modalAssetData.ugcRights && modalAssetData.ugcRights.status == 2 ? ()=>exportDonwloadMedia(modalAssetData.postFile, modalAssetData.type, modalAssetData.mediaUrl) : () => this.downloadWarning(modalAssetData) : null}>
                                    <Dropdown.Item>
                                        <i className="ri-download-2-line icon-6 me-1"></i>
                                        <span className="menu-title">Export/Download</span>
                                    </Dropdown.Item>
                                </div>
                                <div className="menu-item" onClick={deleteAsset ? deleteAsset(modalAssetData) : null}>
                                    <Dropdown.Item>
                                        <i className="ri-delete-bin-6-line icon-6 me-1 text-dark"></i>
                                        <span className="menu-title">Move to Trash</span>
                                    </Dropdown.Item>
                                </div>
                            </Dropdown.Menu>
                        </Dropdown> : null}
                    </div>
                </div>
                <div className="assets_m_header_sidebar d-flex justify-content-between align-items-center px-lg-3 px-2 h-100 bg-white">
                    <span className="text-gray-800 fs-7 me-2 d-none d-lg-flex">Details</span>
                    <button type="button" className="btn-close ms-0 text-light" onClick={this.handleClose}></button>
                </div>
            </Modal.Header>
            <Modal.Body className="p-0 d-flex align-items-center border-0 flex-wrap">
                <div className="assets_modal_inner flex-column flex-grow-1 block-center p-lg-4 p-xxl-5 position-relative mb-2 mb-lg-0 overflow-hidden">
                    <button type="button" className="assets_modal_arrow_left btn btn-secondary rounded-circle position-absolute box-shadow p-0" onClick={this.props.slideAssets(currentIndex - 1)}>
                        <i className="ri-arrow-left-s-line icon-2 me-0"></i>
                    </button>
                    <Suspense fallback={<></>}>
                        <ModalMedia modalAssetData={modalAssetData} networkData={networkData} key={modalAssetData.id} />
                    </Suspense>
                    <button type="button" className="assets_modal_arrow_right btn btn-secondary rounded-circle position-absolute box-shadow p-0" onClick={this.props.slideAssets(currentIndex + 1)}>
                        <i className="ri-arrow-right-s-line icon-2 me-0"></i>
                    </button>
                </div>
                <Suspense fallback={<></>}>
                    {modalAssetData ? <AssetsModalAside modalAssetData={modalAssetData} collections={collections} isTrash={isTrash} postPostUpdate={postPostUpdate} key={modalAssetData.id} modalCallback={this.props.modalCallback} /> : null}
                </Suspense>
            </Modal.Body>
        </Modal>
    }
}

const mapStateToProps = (state) => {
    return {
        networkData: state.networks.networkData,
    }
}

export default connect(mapStateToProps)(AssetsModal);